<template>
  <div>
    <section class="head">
      <div><a @click="goBack" class="iconfont back"> &#xe614; </a></div>
      <h1>Reviews List</h1>
      <div class="chartWrap">
        <a class="iconfont cart" @click="$router.push('cart')"> &#xe669; </a>
      </div>
    </section>
    <div class="reviewsOrder">
      <ul>
        <li v-for="(item, index) in orderDetail" :key="index">
          <div class="starWrap">
            <div class="leftWrap">
              <img :src="item.img0" />
            </div>
            <div class="rightWrap">
              <span>Women’s Hardsided Luggage SlPreppy</span>
              <p>Blue; M</p>
            </div>
          </div>
          <div class="comment">
            <div class="star">
              <van-rate
                v-model="item.points"
                :size="15"
                color="#f44"
                void-icon="star"
                void-color="#eee"
              /><span>{{ item.points }}</span>
              <span class="time">23.12.2018 12:56:59</span>
            </div>
            <div class="labelWrap">
              <div
                :class="['setmeal', label.checked ? 'red' : '']"
                v-for="(label, _i) in item.labelList"
                @click="selectLabel(label)"
                :key="_i"
              >
                {{ label.name }}
              </div>
            </div>
            <span class="leaving">
              <van-field
                v-model="item.review"
                required
                clearable
                label="Review"
                placeholder="min 50 characters"
              />
            </span>
          </div>
        </li>
      </ul>
    </div>
    <van-button type="danger" @click="submitReview">Submit</van-button>
  </div>
</template>

<script>
import { writeCommentProInfo, commentSubmit } from "@/api/product";
export default {
  name: "reviewsOrder",
  data() {
    return {
      value: "5",
      orderNo: this.$route.query.orderNo,
      orderDetail: []
    };
  },
  computed: {},
  mounted() {
    this.init();
  },
  methods: {
    init() {
      writeCommentProInfo({
        orderNo: this.orderNo
      }).then(res => {
        this.orderDetail = res.data.result;
      });
    },
    changeSort(val) {
      this.sortValue = val;
      this.sortVisible = false;
    },
    goBack() {
      this.$router.back(-1);
    },
    submitReview() {
        let data=[];
        this.orderDetail.forEach(e=>{
            if(e.review&&e.points){

                data.push({
                    spuCode:e.spuCode,
                    orderNo:this.orderNo,
                    comment:e.review,
                    points:e.points,
                    labelIdList:e.labelList.filter(t=>{return t.checked;}).map(t=>{return t.id;})
                });
            }
        });
      commentSubmit(data).then(res => {});
    },
    selectLabel(label) {
        console.log(1)
      this.$set(label, "checked",! label.checked);
    }
  }
};
</script>

<style lang="scss" scoped>
.reviewsOrder {
  ul {
    padding-top: 10px;
    background-color: #f6f6f6;
    display: inline-block;
  }
  li {
    margin-bottom: 10px;
    padding: 0 4%;
    height: 219px;
    background-color: white;
    padding-bottom: 10px;
  }
}

.starWrap {
  width: 100%;
  border-bottom: 1px solid #f6f6f6;
}
.starWrap {
  height: 90px;
  display: flex;
  align-items: center;
}
.leftWrap img {
  width: 30px;
  height: 60px;
}
.leftWrap {
  background-color: #f2f2f2;
  width: 18%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80%;
}
.rightWrap {
  width: 82%;
  height: 80%;
  padding-left: 20px;
  span {
    width: 218px;
    font-size: 14px;
    display: block;
    color: #111111;
  }
  p {
    font-size: 14px;
    color: #999999;
    padding-top: 10px;
  }
}
.scoreWrap {
  display: flex;
  align-items: center;
  padding: 10px 0 5px 10px;
}
.scoreWrap span {
  color: #e33946;
  padding-left: 10px;
}
.tap {
  color: #2b87d5;
  padding-left: 20px;
  font-size: 11px;
}
.comment {
  width: 100%;
  padding: 0 0px 10px 0px;
  height: 127px;
}
.star {
  display: -webkit-box;
  position: relative;
  padding-top: 10px;
  span {
    color: #e33946;
    font-size: 15px;
    padding-right: 5px;
  }
  .van-rate {
    padding-top: 2.5px;
  }
  .time {
    position: absolute;
    font-size: 12px;
    color: #999999;
    display: block;
    right: 0;
  }
}
.labelWrap {
  width: 100%;
  display: table;
}
.setmeal {
  width: 65px;
  height: 23px;
  font-size: 11px;
  line-height: 23px;
  border: #666666 0.5px solid;
  border-radius: 2px;
  text-align: center;
  color: #666666;
  float: left;
  margin-top: 10px;
  margin-right: 15px;
}
.red {
  border: #e33946 1px solid;
  color: #e33946;
}
.leaving {
  display: block;
  font-size: 14px;
  padding-top: 12px;
  padding-bottom: 20px;
}
</style>



